/* Ð¡Ñ‚Ð¸Ð»Ð¸ Ð´Ð»Ñ Ð¿Ð¾Ð´Ð»Ð¾Ð¶ÐºÐ¸ */

.overlay {
  /* Ð¡ÐºÑ€Ñ‹Ð²Ð°ÐµÐ¼ Ð¿Ð¾Ð´Ð»Ð¾Ð¶ÐºÑƒ  */
  opacity: 0;
  visibility: hidden;
  padding: 15px;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}

/* Ð¡Ñ‚Ð¸Ð»Ð¸ Ð´Ð»Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ñ‹Ñ… Ð¾ÐºÐ¾Ð½ */

.modal {
  /* Ð¡ÐºÑ€Ñ‹Ð²Ð°ÐµÐ¼ Ð¾ÐºÐ½Ð°  */
  opacity: 0;
  visibility: hidden;

  /*  Ð£ÑÑ‚Ð°Ð½Ð¾Ð²Ð°ÐµÐ¼ ÑˆÐ¸Ñ€Ð¸Ð½Ñƒ Ð¾ÐºÐ½Ð°  */
  width: 100%;
  max-width: 500px;
  @media screen and (min-width: $tablet) {
    width: 600px;
  }
  @media screen and (min-width: $desktop) {
    width: 800px;
  }

  /*  Ð¦ÐµÐ½Ñ‚Ñ€Ð¸Ñ€ÑƒÐµÐ¼ Ð¸ Ð·Ð°Ð´Ð°ÐµÐ¼ z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30; /* Ð”Ð¾Ð»Ð¶ÐµÐ½ Ð±Ñ‹Ñ‚ÑŒ Ð²Ñ‹ÑˆÐµ Ñ‡ÐµÐ¼ Ñƒ Ð¿Ð¾Ð´Ð»Ð¾Ð¶ÐºÐ¸*/

  /*  ÐŸÐ¾Ð±Ð¾Ñ‡Ð½Ñ‹Ðµ ÑÑ‚Ð¸Ð»Ð¸   */
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 15px;
  border-radius: 3px;
  background-color: #fff;
  transition: 0.3s all;
}

.modal__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;

  line-height: 1.17;
  margin-bottom: 15px;
  text-align: center;
  text-transform: uppercase;

  color: var(--accent-color);
}
.modal__text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
}

/* Ð¡Ñ‚Ð¸Ð»Ð¸ Ð´Ð»Ñ Ð°ÐºÑ‚Ð¸Ð²Ð½Ñ‹Ñ… ÐºÐ»Ð°ÑÑÐ¾Ð² Ð¿Ð¾Ð´Ð»Ð¾Ð¶ÐºÐ¸ Ð¸ Ð¾ÐºÐ½Ð° */

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}

/* Ð¡Ñ‚Ð¸Ð»Ð¸ Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ñ */

.modal__cross {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 20px;
  right: 20px;
  fill: #444;
  cursor: pointer;
}

.modal__cross:hover,
modal__cross:focus {
  fill: var(--accent-color);
}
